@import url('https://fonts.googleapis.com/css2?family=Kdam+Thmor+Pro&family=Poppins:wght@600&display=swap');
body{
  background:rgb(241, 241, 241);

}
.App {
  text-align: center;
  width:100%;
  height:100%;
  
}
*, *::before, *::after{
  margin:0;
  padding:0;
  box-sizing: border-box;
}

.countriesContainer{
  /* display:grid;
  grid-template-columns:repeat(3, auto);
  font-family:sans-serif; */
display:flex;
justify-content: center;
align-items: center;
flex-direction:column;
flex-wrap:wrap;
width:100vw;



}
.rows{
 display:grid;
 grid-template-columns:repeat(3, auto);
 row-gap:20px;
 margin:auto;
 /* background:green; */
 width:100%;
}
.flag{
  width:120px;
  border:1px solid rgba(255,255,255,0.5)
}
.countryCard{
  background:rgb(235,235,235);
  margin:10px;
  width:460px;
  height:240px;
  border-radius:8px;
  transition:0.3s;
  cursor:pointer;
  border:1px solid white;
  box-shadow:0 0 10px rgba(0,0,0,0.3);
  position: relative
}
.countryCard:hover{
  box-shadow: 0 0 2px rgba(0,0,0,0.3);
  transform:scale(1.01);
  background:rgb(245, 245, 245);
}
.noResults{
  width:100vwh;
  height:100vh;
  background:rgb(255, 48, 16);
  transition:0.3s
}
.population{
  background:rgb(33, 33, 200);
 color:white;
}
.recovered{
  background:rgb(2, 146, 45);
  color:white
}
.data{
  font-family:sans-serif;
  transform:scale(0.85);
  height:55px;
  border-radius:4px;
  padding:4px;
  color:white;
}
.todayCases{
  background:orangered;
}
.todayRecovered{
  background:rgb(196, 196, 56);
}
.deaths{
  background:rgb(221, 31, 31);
}
.tests{
  background:rgb(152, 152, 245)
}
.title{
  font-family:'Poppins',sans-serif
}
.name{
  font-family:'Poppins',sans-serif;
  color:black;
}
.tryAgain{
  font-family:'Montserrat',sans-serif;
  color:white;
}
.noResultsH1{
  color:white;
  font-family:'Poppins',sans-serif
}
.results{
  font-family:'Montserrat',sans-serif
}
.filterInput{
  background: rgba( 255, 255, 255, 0.1 );
  backdrop-filter: blur( 4px );
border-radius:3px;
border:1px solid black;
cursor:pointer;
text-align:center;
transform:scale(1.1)
}
.filterInput::placeholder{
  text-align: center;
}
@media (max-width:500px){
  .countryCard{
    transform:scale(0.85);
    margin:0 auto
  }
}
@media (max-width:450px){
  .countryCard{
    transform:scale(0.75);
  

  }
}